n  .username {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    margin-top: -1px;\r\n  }\r\n\r\n  .description {\r\n    color: $gray-600;\r\n    font-size: 13px;\r\n    margin-top: -3px;\r\n  }\r\n\r\n  &.user-block-sm {\r\n    img {\r\n      width: $img-size-sm;\r\n      height: $img-size-sm;\r\n    }\r\n\r\n    .username,\r\n    .description,\r\n    .comment {\r\n      margin-left: 40px;\r\n    }\r\n\r\n    .username {\r\n      font-size: 14px;\r\n    }\r\n  }\r\n}\r\n\r\n// Image sizes\r\n.img-sm,\r\n.img-md,\r\n.img-lg {\r\n  float: left;\r\n}\r\n\r\n.img-sm {\r\n  height: $img-size-sm;\r\n  width: $img-size-sm;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-sm + $img-size-push;\r\n  }\r\n}\r\n\r\n.img-md {\r\n  width: $img-size-md;\r\n  height: $img-size-md;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-md + $img-size-push;\r\n  }\r\n}\r\n\r\n.img-lg {\r\n  width: $img-size-lg;\r\n  height: $img-size-lg;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-lg + $img-size-push;\r\n  }\r\n}\r\n\r\n// Image bordered\r\n.img-bordered {\r\n  border: 3px solid $gray-500;\r\n  padding: 3px;\r\n}\r\n\r\n.img-bordered-sm {\r\n  border: 2px solid $gray-500;\r\n  padding: 2px;\r\n}\r\n\r\n// Rounded and Circle Images\r\n.img-rounded {\r\n  @include border-radius($border-radius)\r\n}\r\n\r\n.img-circle {\r\n  @include border-radius(50%);\r\n}\r\n\r\n// Image sizes\r\n.img-size-64,\r\n.img-size-50,\r\n.img-size-32 {\r\n  height: auto;\r\n}\r\n\r\n.img-size-64 {\r\n  width: 64px;\r\n}\r\n\r\n.img-size-50 {\r\n  width: 50px;\r\n}\r\n\r\n.img-size-32 {\r\n  width: 32px;\r\n}\r\n\r\n// Block sizes\r\n.size-32,\r\n.size-40,\r\n.size-50 {\r\n  display: block;\r\n  text-align: center;\r\n}\r\n\r\n.size-32 {\r\n  height: 32px;\r\n  line-height: 32px;\r\n  width: 32px;\r\n}\r\n\r\n.size-40 {\r\n  height: 40px;\r\n  line-height: 40px;\r\n  width: 40px;\r\n}\r\n\r\n.size-50 {\r\n  height: 50px;\r\n  line-height: 50px;\r\n  width: 50px;\r\n}\r\n\r\n// General attachemnt block\r\n.attachment-block {\r\n  background: $gray-100;\r\n  border: 1px solid $card-border-color;\r\n  margin-bottom: 10px;\r\n  padding: 5px;\r\n\r\n  .attachment-img {\r\n    float: left;\r\n    height: auto;\r\n    max-height: 100px;\r\n    max-width: 100px;\r\n  }\r\n\r\n  .attachment-pushed {\r\n    margin-left: 110px;\r\n  }\r\n\r\n  .attachment-heading {\r\n    margin: 0;\r\n  }\r\n\r\n  .attachment-text {\r\n    color: $gray-700;\r\n  }\r\n}\r\n\r\n// Overlays for Card, InfoBox & SmallBox\r\n.card,\r\n.overlay-wrapper,\r\n.info-box,\r\n.small-box {\r\n  // Box overlay for LOADING STATE effect\r\n  > .overlay,\r\n  > .loading-img {\r\n    height: 100%;\r\n    left: 0;\r\n    position: absolute;\r\n    top: 0;\r\n    width: 100%;\r\n  }\r\n\r\n  .overlay {\r\n    @include border-radius($border-radius);\r\n    align-items: center;\r\n    background: rgba($white, 0.7);\r\n    display: flex;\r\n    justify-content: center;\r\n    z-index: 50;\r\n\r\n    > .fa,\r\n    > .fas,\r\n    > .far,\r\n    > .fab,\r\n    > .glyphicon,\r\n    > .ion {\r\n      color: $gray-800;\r\n    }\r\n\r\n    &.dark {\r\n      background: rgba($black, 0.5);\r\n\r\n      > .fa,\r\n      > .fas,\r\n      > .far,\r\n      > .fab,\r\n      > .glyphicon,\r\n      > .ion {\r\n        color: $gray-400;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.tab-pane {\r\n  // Box overlay for LOADING STATE effect on Tab Panels\r\n  > .overlay-wrapper {\r\n    position: relative;\r\n    > .overlay {\r\n      border-top-left-radius: 0;\r\n      border-top-right-radius: 0;\r\n      flex-direction: column;\r\n      margin-top: -$card-spacer-x;\r\n      margin-left: -$card-spacer-x;\r\n      height: calc(100% + 2 * #{$card-spacer-x});\r\n      width: calc(100% + 2 * #{$card-spacer-x});\r\n\r\n      &.dark {\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Ribbon\r\n.ribbon-wrapper {\r\n  height: $ribbon-wrapper-size;\r\n  overflow: hidden;\r\n  position: absolute;\r\n  right: -2px;\r\n  top: -2px;\r\n  width: $ribbon-wrapper-size;\r\n  z-index: 10;\r\n\r\n  &.ribbon-lg {\r\n    height: $ribbon-lg-wrapper-size;\r\n    width: $ribbon-lg-wrapper-size;\r\n\r\n    .ribbon {\r\n      right: $ribbon-lg-right;\r\n      top: $ribbon-lg-top;\r\n      width: $ribbon-lg-width;\r\n    }\r\n  }\r\n\r\n  &.ribbon-xl {\r\n    height: $ribbon-xl-wrapper-size;\r\n    width: $ribbon-xl-wrapper-size;\r\n\r\n    .ribbon {\r\n      right: $ribbon-xl-right;\r\n      top: $ribbon-xl-top;\r\n      width: $ribbon-xl-width;\r\n    }\r\n  }\r\n\r\n  .ribbon {\r\n    box-shadow: 0 0 $ribbon-border-size rgba($black, .3);\r\n    font-size: $ribbon-font-size;\r\n    line-height: $ribbon-line-height;\r\n    padding: $ribbon-padding;\r\n    position: relative;\r\n    right: $ribbon-right;\r\n    text-align: center;\r\n    text-shadow: 0 -1px 0 rgba($black, .4);\r\n    text-transform: uppercase;\r\n    top: $ribbon-top;\r\n    transform: rotate(45deg);\r\n    width: $ribbon-width;\r\n\r\n    &::before,\r\n    &::after {\r\n      border-left: $ribbon-border-size solid transparent;\r\n      border-right: $ribbon-border-size solid transparent;\r\n      border-top: $ribbon-border-size solid #9e9e9e;\r\n      bottom: -$ribbon-border-size;\r\n      content: '';\r\n      position: absolute;\r\n    }\r\n\r\n    &::before {\r\n      left: 0;\r\n    }\r\n\r\n    &::after {\r\n      right: 0;\r\n    }\r\n  }\r\n}\r\n\r\n// Scroll To Top\r\n.back-to-top {\r\n  bottom: 1.25rem;\r\n  position: fixed;\r\n  right: 1.25rem;\r\n  z-index: $zindex-control-sidebar + 1;\r\n\r\n  &:focus {\r\n    box-shadow: none;\r\n  }\r\n}\r\n\r\n// Pre\r\npre {\r\n  padding: .75rem;\r\n}\r\n\r\n// Blockquotes styles\r\nblockquote {\r\n  background: $white;\r\n  border-left: .7rem solid $primary;\r\n  margin: 1.5em .7rem;\r\n  padding: 0.5em .7rem;\r\n\r\n  .box & {\r\n    background: $gray-200;\r\n  }\r\n\r\n  p:last-child {\r\n    margin-bottom: 0;\r\n  }\r\n\r\n  h1,\r\n  h2,\r\n  h3,\r\n  h4,\r\n  h5,\r\n  h6 {\r\n    color: $primary;\r\n    font-size: 1.25rem;\r\n    font-weight: 600;\r\n  }\r\n\r\n  @each $color, $value in $theme-colors {\r\n    &.quote-#{$color} {\r\n      border-color: $value;\r\n\r\n      h1,\r\n      h2,\r\n      h3,\r\n      h4,\r\n      h5,\r\n      h6 {\r\n        color: $value;\r\n      }\r\n    }\r\n  }\r\n\r\n  @each $color, $value in $colors {\r\n    &.quote-#{$color} {\r\n      border-color: $value;\r\n\r\n      h1,\r\n      h2,\r\n      h3,\r\n      h4,\r\n      h5,\r\n      h6 {\r\n        color: $value;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// Tab Custom Content\r\n\r\n.tab-custom-content {\r\n  border-top: $nav-tabs-border-width solid $nav-tabs-border-color;\r\n  margin-top: .5rem;\r\n  padding-top: .5rem;\r\n}\r\n\r\n.nav + .tab-custom-content {\r\n  border-top: none;\r\n  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;\r\n  margin-top: 0;\r\n  margin-bottom: .5rem;\r\n  padding-bottom: .5rem;\r\n}\r\n\r\n\r\n// Badge BTN Style\r\n.badge-btn {\r\n  border-radius: $button-border-radius-xs;\r\n  font-size: $button-font-size-xs;\r\n  font-weight: 400;\r\n  padding: $button-padding-y-xs*2 $button-padding-x-xs*2;\r\n}\r\n\r\n.badge-btn.badge-pill {\r\n  padding: .375rem .6rem;\r\n}\r\n","//\n// Misc: Print\n//\n\n@media print {\n  //Add to elements that you do not want to show when printing\n  .no-print {\n    display: none !important;\n  }\n\n  //Elements that we want to hide when printing\n  .main-sidebar,\n  .main-header,\n  .content-header {\n    @extend .no-print;\n  }\n\n  //This is the only element that should appear, so let's remove the margins\n  .content-wrapper,\n  .main-footer {\n    @include translate(0, 0);\n    margin-left: 0 !important;\n    min-height: 0 !important;\n  }\n\n  .layout-fixed .content-wrapper {\n    padding-top: 0 !important;\n  }\n\n  //Invoice printing\n  .invoice {\n    border: 0;\n    margin: 0;\n    padding: 0;\n    width: 100%;\n  }\n\n  .invoice-col {\n    float: left;\n    width: 33